<template>
  <div id="AccountSettingPassword">
    <div class="card" ref="card">
      <div class="card-body">
        <div class="card-title">
          <span class="title">修改密码</span>
          <hr />
        </div>

        <vs-input
          primary
          placeholder="请输入原密码"
          v-model="form.oldPassword"
          type="password"
          :visiblePassword="hasVisiblePassword1"
        >
          <template v-if="!form.oldPassword" #message-danger> 必填项 </template>
        </vs-input>
        <vs-input
          primary
          placeholder="请输入新密码"
          v-model="form.password"
          type="password"
          :visiblePassword="hasVisiblePassword2"
          class="mt-3"
        >
          <template v-if="!form.password" #message-danger> 必填项 </template>
        </vs-input>
        <vs-input
          warn
          placeholder="确认新密码"
          v-model="form.confirmPassword"
          type="password"
          :visiblePassword="hasVisiblePassword3"
          class="my-3"
        >
          <template
            v-if="
              (form.password !== form.confirmPassword) | !form.confirmPassword
            "
            #message-danger
          >
            两次密码不一致
          </template>
        </vs-input>
        <vs-button success size="large" @click="ChangeStart" class="w-100"
          >修改密码</vs-button
        >
      </div>
    </div>
    <div class="card mt-3 mb-3">
      <div class="card-body">
        <div class="card-title">
          <span class="title">管理令牌</span>
          <hr />
        </div>
        <span class="text-danger font-weight-bolder text-center">暂未开放</span>
      </div>
    </div>
  </div>
</template>

<script>
import { modification_password } from "@/scripts/auth";
export default {
  name: "Password",
  data() {
    return {
      hasVisiblePassword1: false,
      hasVisiblePassword2: false,
      hasVisiblePassword3: false,
      form: {
        oldPassword: "",
        password: "",
        confirmPassword: "",
      },
      loading: null,
    };
  },
  methods: {
    ChangeStart() {
      if (
        !this.form.oldPassword ||
        !this.form.password ||
        !this.form.confirmPassword
      ) {
        this.$toastx({
          type: "warning",
          title: "请填写完整信息",
        });
        return;
      }
      if (this.form.password !== this.form.confirmPassword) {
        this.$toastx({
          type: "warning",
          title: "两次密码不一致",
        });
        return;
      }

      console.log(this.$swal);
      this.$swal({
        title: "确认修改密码？",
        icon: "warning",
        showCancelButton: true,
        confirmButtonText: "确认",
        cancelButtonText: "取消",
      }).then((resp) => {
        if (resp.isConfirmed) {
          this.loading = this.$vs.loading({
            target: this.$refs.card,
            text: "正在修改...",
          });
          modification_password({
            old_pwd: this.form.oldPassword,
            new_pwd: this.form.password,
          }).then(() => {
            this.loading.close();
            this.loading = null;
          });
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>
#AccountSettingPassword {
  display: flex;
  flex-direction: column;
  .card .title {
    font-weight: bolder;
    font-size: 18px;
  }

  .card form:last-child {
    display: flex;
    flex-direction: column;
    .btn {
      align-self: flex-end;
    }
  }
}

.mini {
  height: 15px;
  width: 15px;
}
</style>
